.global_heading {
  display: flex;
  justify-content: space-between;
  align-items: center;

  h2{
    display: inline-block;
    margin-bottom: 0;
    color: $primary-color-yellow;
    font-size: 4rem;
    font-weight: normal;
    position: relative;
    &::before{
      content: "";
      width: 100%;
      height: 1px;
      background-color: $primary-color-yellow;
      position: absolute;
      left: -100px;
      bottom: 7px;
    }
    &::after{
      content: "";
      width: 100%;
      height: 1px;
      background-color: $primary-color-yellow;
      position: absolute;
      left: 0;
      bottom: 7px;
    }
  }

  .auction_nav{
    display: flex;
    justify-content: space-between;
    align-items: center;    
    width: 8%;
    margin-right: 8%;
    .slider_nav{            
      position: static;
      margin-top: 0;
      background-color: $primary-color-yellow;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      padding: 18px;      
      &::after{
        font-size: 12px;
        font-weight: bold;
        color: $primary-color;
      }        
    }
  }
}